
////////////////////
// Horizontal Nav
////////////////////
@media (min-width: 64rem) {
    [data-layout="horizontal"] {
        --dz-side-menu-direction : row;
        --dz-side-info : none;
        --dz-side-copyright : none;
        --dz-side-nav-padding : .7rem 1.4rem .7rem 0.85rem;
        --dz-side-sub_nav-padding : .5rem 1.2rem;
        --dz-sidebar-pos : relative;

        &[data-sidebar-style="mini"]{
            --dz-side-nav-padding : 1rem;

            .deznav .metismenu > li {
                padding: 0;
                & > a{
                    i{
                        margin: 0;
                        padding: 0;
                    }
                }
            }

        }
        &[data-sidebar-style="modern"]{
            --dz-sidebar-width : 17.5rem;

            .deznav .metismenu > li:hover {
                background:transparent;
                
            }
        }
        &[data-sidebar-style="full"],
        &[data-sidebar-style="icon-hover"]{
            .deznav{
                .metismenu{
                    &>li{
                        &>a{
                            .nav-text{
                                margin-right: 15px;
                            }
                            &.has-arrow{
                                --dz-side-nav-padding : .7rem 1.4rem .7rem 0.85rem;
                            }
                        }
                    }
                }
            }
        }
        
        .nav-header {
            .nav-control {
                display: none;
            }

            .brand-logo {

                padding-left: 2.5rem;
				padding-right: 2.5rem;
            }
        }

        .header {
            width: 100%;
			padding-top:0;
            .nav-control {
                display: none;
            }
        }

        .deznav {
            width: 100%;
            position: relative;
            height: auto;
            padding-bottom: 0;
            top: 0;
            z-index: 2;
			
			
            .slimScrollDiv {
                overflow: visible!important;

                .deznav-scroll {
                    overflow: visible!important;
                }
            }
			.deznav-scroll {
				overflow: visible!important;
			}
            .slimScrollBar {
                display: none !important;
            }

            .nav-user,
            .nav-label {
                display: none;
            }
			
            .metismenu {
                flex-direction: row;
				padding:10px 40px;
				margin-bottom: 0;
				padding-right:1.25rem;
                display: inline-flex;
                flex-wrap: wrap;	
                .collapse.in {
                    display: none;
                }
				ul{
					border-left:0;
				}
                li {
                    flex-direction: column;
                    position: relative;
					a{
                        margin: 0.125rem 0.125rem;
                        .nav-text{
                            font-size: 14px;
                        }
						&>i{
							height: auto;
							width: auto;
							padding: 0;
							position:relative;
							font-size: 1.2rem;
							line-height: 1;
						}
					}
                    &:hover {
                        &>ul {
                            display: block;
                        }
                    }
                    
                    &>ul {
                        position: absolute;
                        height: auto !important;
                        top: 100%;
                        width: 100%;
                        min-width: 13.75rem;
                        z-index: 999;
                        left: auto;
                        right: auto;
                        padding: 0.5rem 0;
                        display: none;
                        box-shadow: 0px 0px 40px 0px rgba(82, 63, 105, 0.1);
						margin:0;
						background:$white;
                        border-radius: 12px;
							
                        li {
                            padding: 0;
    
                            a {
                                transition: all .4s ease-in-out;
                                padding: 8px 20px 8px 20px;
                                margin-left: -.1rem;
                                &:hover {
                                    border-radius: .4rem;
                                }
                            }
                        }
                        
                        ul {
                            left: 100%;
                            top: 0;
							box-shadow: 0px 0px 40px 0px rgba(82, 63, 105, 0.1);
							
                        }
                    }
                }

                &>li {
                    flex: 0 0 auto;
                    position: relative;
					&:hover,
					&.mm-active{
						padding:0;			
						& > a{
							background-color:var(--bs-primary);
							color:$white;
                            border-radius: var(--bs-border-radius);
							
                            .nav-text{
                                font-size: 14px;
                            }
							i{
								color:$white;
							}
						}
					}
                    &>a {
						border-radius:50%;
                       
						
                        i{
                            padding:0 0.1375rem 0 0;
								
                            
                        }
                        .nav-badge {
                            display: none;
                        }
                        &:after {
                            right: 10px;
                            transform: rotate(-135deg) translateY(-50%);
                        }
                    }
                    &:hover {
						border-color:transparent;
						
                        & > ul {
                            display: flex !important;
                            flex-direction: column;
                            flex-wrap: wrap;
                            height: auto !important;
							box-shadow: 5px 5px 30px 0px rgba(20, 0, 30, 0.1);
							border-radius: 12px;
                        }
                    }
                    &>ul {
                        &>li {
                            &:hover {
                                ul.collapse {
                                    display: block!important;
                                    position: absolute;
                                    left: auto!important;
                                    right: -100%!important;
                                    top: 0!important;
                                }
                            }
                        }
                    }
                    &:nth-last-child(-n + 5) {
                        &>ul {
                        left: auto;
                        right: -100;
                            &>li {
                                &:hover {
                                    ul.collapse {
                                        right: auto!important;
                                        left: -100%!important;
                                    }
                                }
                            }
                        }
                    }
                    &:last-child {
                        &>ul {
                            ul {
                                left: -100%;
                            }
                        }
                    }
                    
                }
            }
			.copyright,
			.book-box{
				display:none;
			}
        }
		
        .content-body {
            margin-left: 0;
			.container-fluid{
				padding-top:2.5rem;
			}
            .page-titles {
                margin-left: 0!important;
                margin-right: 0!important;
                margin-bottom: 1.875rem;
            }
        }

        .footer {
            margin-left: 0;
            margin: 0 auto;
            padding-left: 0;
        }
    }

    [data-header-position="fixed"][data-layout="horizontal"] {
        .deznav {
            top: var(--dz-header-height);
        }
    } //ok

    [data-header-position="fixed"][data-sidebar-position="fixed"] {
        --dz-sidebar-pos : fixed;
    } //ok

    [data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"] {
        .content-body {
            padding-top: 9.5rem;
        }
    } //ok
	[data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"][data-sidebar-style="modern"] {
        .content-body {
            padding-top: 10.5rem;
        }
    } //ok
	
    [data-layout="horizontal"][data-container="boxed"] {

        .footer {
            margin-left: 0;
            max-width: 74.9375rem;
            margin: 0 auto;
        }
    }

    [data-layout="horizontal"][data-container="wide"] {
        .page-titles {
            margin-left: -1.875rem;
            margin-right: -1.875rem;
        }
    }

    [data-layout="horizontal"][data-sidebar-style="modern"] {
		 .deznav {
			.header-profile{
				margin-bottom:0;
				img{
					height: 3.75rem;
					width: 3.75rem;
					margin-bottom:0!important;
				}
				&>a.nav-link{
					border:0.0625rem solid $border-color;
					padding: 0.25rem 0.25rem;
					border-radius:3rem;
				}
			} 
		 }
	}
    [data-layout="horizontal"][data-sidebar-style="compact"] {
        --dz-sidebar-width : 17.5rem;
        --dz-side-nav-padding: 1rem 1.2rem .75rem;
        --dz-side-sub_nav-padding: .5rem 1.5rem;
        .page-titles {
            margin-top: 0;
        }
        
        .deznav {
			.header-profile{
				margin-bottom:0;
				img{
					height: 3.75rem;
					width: 3.75rem;
					margin-bottom:0!important;
				}
				&>a.nav-link{
					border:0.0625rem solid $border-color;
				}
			} 
            .metismenu {
	
                &>li {
                    &>ul {
                        top: 100%;
                    }
                    &>a {
						.badge-xs{
							&.style-1{
								margin-left:0px;
								
							}
						}

                        &::after {
                            display: none;
                        }
						.nav-text{
							margin-top: 0.3125rem;
						}
						&>i{
							margin:0;
                            display: block;
							line-height:1;
							padding:0;
							background:transparent;
							margin-bottom: 0;
							width: 3.125rem;
							height: 3.125rem;
							line-height: 3.125rem;
							border-radius: 0.375rem;
							margin-left: auto;
							margin-right: auto;
							
						}
                    }
                    li {
                        text-align: left;
                    }
                }
            }
        }
    }

    [data-sidebar-style="mini"][data-layout="horizontal"] {
        .nav-header {
            width: 7.75rem;
			padding-left: 2.5rem;
			padding-right: 2.5rem;
			
			    .brand-logo {
				    justify-content: start;
					padding-left: 0;
					padding-right: 0;
				}
        }
    
        .header {
            width: 100%;
            padding-left: 7.75rem;
        }

        .metismenu {
            &>li {
				a{
					width: auto;
				}
                &:hover {
                    a {
                        .nav-text {
                            display: none;
                        }
                    }
                }
            }
        }
    }
	
	[data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"][data-sidebar-style="full"],
    [data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"][data-sidebar-style="compact"] {
        .content-body {
            padding-top: 10.5rem;
        }
    }
	[data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"][data-sidebar-style="full"][data-container="boxed"]{
        .content-body {
            padding-top: 13.5rem;
        }
    }
	[data-header-position="fixed"][data-layout="horizontal"][data-sidebar-position="fixed"][data-sidebar-style="full"]{
		.content-body {
            padding-top: 9rem;
			@media screen and (max-width: 1367px) {
				padding-top: 13.5rem;
			}
        }
	}

    [data-sidebar-position="fixed"][data-layout="horizontal"] {
        .deznav.fixed {
            position: fixed;
			padding: 0 0.9375rem;
            left: 0;
            top: 0;
			border-radius:0;
			width:100%;
        }
    }

}
